* {
  padding: 0;
  margin: 0; }

.is-hidden {
  display: none; }

body {
  font-size: 16px;
  font-family: 'Roboto', sans-serif; }

.theme {
  background: #282a36;
  color: #f8f8f2;
  padding: 15px; }

pre.language-markup {
  display: none; }

.resume {
  max-width: 1200px;
  min-width: 360px; }
  .resume__header {
    display: flex;
    flex-direction: column;
    align-items: center; }
    .resume__header-title {
      font-family: 'BioRhyme Expanded', serif;
      display: flex;
      flex-direction: column;
      text-align: center;
      font-size: 2rem;
      margin-top: 15px; }
      .resume__header-title span[class*=group]:not(:last-of-type) {
        margin-right: 15px; }
      .resume__header-title .group-0 {
        color: #50fa7b; }
      .resume__header-title .group-1 {
        color: #50fa7b; }
      .resume__header-title .group-2 {
        color: #ffb86c;
        text-decoration: underline;
        text-underline-position: under; }
    .resume__header-button {
      padding: 20px;
      width: 90%;
      display: block;
      margin: 0 auto;
      background: #ff3c3c;
      color: #f8f8f2;
      border: inset 1px rgba(248, 248, 242, 0.7);
      border-radius: 4px;
      font-size: 1.5rem;
      font-weight: 300;
      text-shadow: -1px 1px 1px #282a36; }
      .resume__header-button:hover {
        cursor: pointer;
        background: #ff2222; }
      .resume__header-button:focus {
        outline: #ff5555; }
      .resume__header-button:active {
        background: #d50000;
        border: 0; }
  .resume-content {
    margin-top: 30px; }
